import { useState, useMemo } from 'react';
import { Accordion } from '@/components/ui/accordion';
import Link from 'next/link';
import { ChevronLeft, Search } from 'lucide-react';

export default function HelpCenter() {
  const [searchQuery, setSearchQuery] = useState('');
  
  const faqItems = [
    {
      question: 'How do I reset my password?',
      answer: 'To reset your password, go to the login page and click on "Forgot Password". Follow the instructions sent to your email to create a new password.'
    },
    {
      question: 'How can I update my profile information?',
      answer: 'You can update your profile information by going to the "My Account" section in the app. Click on the edit icon next to the information you want to update.'
    },
    {
      question: 'What payment methods do you accept?',
      answer: 'We accept all major credit cards, PayPal, and bank transfers. You can view all available payment methods in the billing section of your account.'
    },
    {
      question: 'How do I contact customer support?',
      answer: 'You can contact our customer support team 24/7 through the in-app chat or by emailing support@example.com. We typically respond within 24 hours.'
    },
    {
      question: 'Is my data secure?',
      answer: 'Yes, we take data security very seriously. All your data is encrypted both in transit and at rest. We use industry-standard security measures to protect your information.'
    }
  ];
  

  // Filter FAQ items based on search query
  const filteredFaqItems = useMemo(() => {
    if (!searchQuery) return faqItems;
    const query = searchQuery.toLowerCase();
    return faqItems.filter(item => 
      item.question.toLowerCase().includes(query) || 
      item.answer.toLowerCase().includes(query)
    );
  }, [faqItems, searchQuery]);

  // Group FAQ items by category
  const faqCategories = useMemo(() => {
    return [
      {
        title: 'Account & Security',
        items: filteredFaqItems
      },
      {
        title: 'Billing & Payments',
        items: [
          {
            question: 'How can I update my payment method?',
            answer: 'You can update your payment method in the Billing section of your account settings.'
          },
          {
            question: 'When will I be charged for my subscription?',
            answer: 'Subscriptions are billed on a monthly or yearly basis, depending on your plan.'
          }
        ].filter(item => 
          searchQuery === '' || 
          item.question.toLowerCase().includes(searchQuery.toLowerCase()) || 
          item.answer.toLowerCase().includes(searchQuery.toLowerCase())
        )
      },
      // Add other categories here with their respective items
    ].filter(category => category.items.length > 0);
  }, [filteredFaqItems, searchQuery]);

  return (
    <div className="min-h-screen bg-[#F0F6FF] p-4 md:p-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex items-center mb-6">
          <Link href="/" className="mr-4 text-gray-600 hover:text-gray-900">
            <ChevronLeft className="w-6 h-6" />
          </Link>
          <h1 className="text-xl font-bold text-gray-900">Help Center</h1>
        </div>
        
        {/* Search Bar */}
        <div className="relative mb-8">
          <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
            <Search className="h-5 w-5 text-gray-400" />
          </div>
          <input
            type="text"
            className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg bg-white shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            placeholder="Search help articles..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          {searchQuery && (
            <button
              onClick={() => setSearchQuery('')}
              className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600"
            >
              <svg className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          )}
        </div>
        
        <p className="text-gray-600 mb-8">
          Find answers to common questions about our service. If you can't find what you're looking for, please contact our support team.
        </p>
        
        {faqCategories.map((category, index) => (
          <div key={index} className="bg-white rounded-lg shadow-sm p-4 md:p-6 mb-8">
            <h2 className="text-xl font-semibold text-gray-800 mb-6">{category.title}</h2>
            <Accordion items={category.items} />
          </div>
        ))}
        
        {faqCategories.length === 0 && (
          <div className="text-center py-12">
            <p className="text-gray-500">No results found for "{searchQuery}"</p>
            <p className="text-gray-400 mt-2">Try different search terms or check back later.</p>
          </div>
        )}
        
        <div className="mt-8 bg-white rounded-lg shadow-sm p-6 text-center">
          <h2 className="text-xl font-semibold text-gray-800 mb-2">Still need help?</h2>
          <p className="text-gray-600 mb-4">Our support team is here to help you with any questions or issues.</p>
          <button className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-6 rounded-lg transition-colors">
            Contact Support
          </button>
        </div>
      </div>
    </div>
  );
}